body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
    padding: 1rem;
    width: 150px;
    background: #000;
    color: #fff;
    font-weight: bolder;
    font-size: 1rem;
    border-radius: 10px;
    border: 1px solid salmon;
    margin: 0 10px;
    cursor: pointer;
}
.home-container {
    display: grid;
    grid-template-rows: 4rem auto fit-content(4rem);
    background: -webkit-linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.5)), url(/frontend-test-II/static/media/jc-dela-cuesta-304543-unsplash.78999eb1.jpg);
    background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.5)), url(/frontend-test-II/static/media/jc-dela-cuesta-304543-unsplash.78999eb1.jpg);
    background-repeat: no-repeat;
    height: 100vh;
}
.button-section-home {
    display: flex;
    justify-content: center;
    align-items: center;
}

.home_header {
    display: flex;
    justify-content: space-between;
    color: #fff;
    padding: 20px;
}

.home_header > div {
    font-weight: bold;
    font-size: 1.1rem;
}

.nav_home a {
    margin: 20px;
    text-decoration: none;
    cursor: pointer;
    font-weight: bold;
    font-size: 1.2rem;
    color: #fff;
}

.nav_home a:hover {
    color: #ccc;
}

footer {
    color: #fff;
    display: flex;
    justify-content: center;
    font-weight: bold;
    font-size: 1.1rem;
}
.container {
    border: 1px solid #ccc;
    width: 50%;
    height: 500px;
    box-shadow: 4px 4px 2px #ccc;
    display: grid;
    grid-template-rows: 4rem auto fit-content(8rem);
    margin: 50px auto;
    max-width: 25rem;
    min-width: 25rem;
}

.header {
    background: -webkit-linear-gradient(#9f15ac, #db6cf7);
    background: linear-gradient(#9f15ac, #db6cf7);
    border: 1px solid #ccc;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navlink {
    margin: 0 10px;
    text-decoration: none;
    color: #fff;
    cursor: pointer;
}

.text-area {
    padding: 1.2rem;
    text-align: right;
}

.text-area:focus {
    outline: none;
}

.screen {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
    overflow: auto;
    margin: 10px 10px;
}

.gifImage{
    width: 10rem;
    max-width: 300px;
    height: 10rem;
    max-height: 200px;
    margin: 10px;
    border-radius: 10px;
    box-shadow: 2px 2px 4px #aaa;
}
.artist {
    background: #FFFFFF;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    text-align: center;
    margin: 20px;
}

.artist li {
    text-decoration: none;
    list-style: none;
    margin: 5px 0;
    border: 1px solid #ccc;
    padding: 5px;
    border-radius: 10px;
}

.image_artist {
    height: 278px;
    border-radius: 8px;
}

.artist_genres div {
    background: #ccc;
    margin: 5px;
    display: inline-block;
    padding: 5px 10px;
    border-radius: 30px;
}

@media screen and (max-width: 800px) {
    .info{
        width: 100%;
    }
}
.related_artists {
    height: 500px;
    overflow: auto;
    margin: 20px;
}

.artist_related_section {
    display: flex;
    border: 1px solid #ccc;
    margin: 10px auto;
    border-radius: 5px;
}

.artist_related_section li{
    list-style: none;
    margin-top: 15px;
}
.images_related_artists {
    width: 150px;
    height: 150px;
}

.header_related_artist {
    padding: 1rem;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

@media (max-width: 950px) {
    .artist_related_section {
        display: flex;
        flex-wrap: wrap;
    }
}
.container_artists {
    display: flex;
    margin: 50px auto;
    border: 1px solid #ccc;
    width: 70%;
    height: auto;
    justify-content: center;
    align-items: center;
}
.back{
    margin: 10px !important;
    background: #aaa;
    text-decoration: none;
    border-radius: 5px;
    color: white;
    padding: 10px;
}

@media screen and (max-width: 800px) {
    .container_artists{
        width: 100%;
        flex-wrap: wrap;
    }
}
.App {
  display: flex;
  justify-content: center;
}

